import type { Dispatch, SetStateAction } from 'react';
import type { Who } from '~/routes';

interface IProps {
	who: Who | 'none';
	setWho: Dispatch<SetStateAction<Who | 'none'>>;
}

export default function Nav({ who, setWho }: IProps) {
	return (
		<div className="p-8 w-full">
			<header className="w-full border-b-2 border-b-emerald-600 pb-8 mb-8">
				<h1 className="text-4xl text-emerald-600 font-mono font-medium pb-4">
					Bookshelf
				</h1>
				<div className="text-xl">
					<label htmlFor="who">Who are you?</label>
					<select
						id="who"
						value={who}
						onChange={e => setWho(e.target.value as Who)}
						className="ml-2"
					>
						<option disabled value="none">
							Please choose
						</option>
						<option value="kate">Kate</option>
						<option value="chris">Chris</option>
					</select>
				</div>
			</header>
		</div>
	);
}
