import { useEffect, useState } from 'react';
import Nav from '~/components/nav';

export type Who = 'chris' | 'kate';
const localStorageKey = 'previous-who';

export default function Index() {
	const [who, setWho] = useState<Who | 'none'>('none');

	useEffect(() => {
		const previousWho = localStorage.getItem(localStorageKey);
		if (previousWho === 'chris' || previousWho === 'kate') {
			setWho(previousWho);
		}
	}, []);

	useEffect(() => {
		if (who) {
			localStorage.setItem(localStorageKey, who);
		}
	}, [who]);

	return (
		<div className="p-8 w-full">
			<Nav who={who} setWho={setWho} />
		</div>
	);
}
